.wrapper {
  @include flex-sb;
  margin: 100px auto;

  // @include big-tablet914 {
  //   @include flex;
  //   flex-direction: column;
  // }

  &__left {
    width: 50%;

    @include big-tablet914 {
      display: none;
    }
  }

  &__right {
    padding-left: 60px;
    padding-right: 122px;
    width: 50%;

    @include medium-desktop {
      padding-right: 60px;
    }

    @include desktop1255 {
      padding-right: 0;
    }

    @include big-tablet914 {
      width: 100%;
    }

    &-title {
      padding-bottom: 15px;
      font-size: 34px;
      font-weight: 300;
      text-transform: uppercase;

      @include mini-desktop1135 {
        font-size: 33px;
      }

      @include mini-desktop1111 {
        font-size: 32px;
      }

      @include mini-desktop1086 {
        font-size: 30px;
      }

      @include tablet1037 {
        font-size: 25px;
      }

      &::after {
        content: "";
        display: block;
        width: 415px;
        height: 4px;
        background-color: var(--orange-color);

        @include mini-desktop1135 {
          width: 400px;
        }

        @include mini-desktop1086 {
          width: 360px;
        }

        @include tablet1037 {
          width: 300px;
        }
      }
    }

    &-text {
      margin-bottom: 50px;
      font-size: 18px;
      font-weight: 300;
      line-height: 1.3;
      color: var(--greyest-color);

      @include medium-desktop {
        padding-right: 50px;
      }

      @include desktop1350 {
        padding-right: 20px;
      }

      @include desktop1290 {
        padding-right: 10px;
      }

      @include desktop1270 {
        padding-right: 0;
      }

      @include desktop1255 {
        padding-right: 30px;
      }

      @include mini-desktop {
        padding-right: 0;
      }

      @include mini-desktop1130 {
        font-size: 16px;
      }
    }
  }
}

.about__right-list {
  color: var(--greyest-color);
}

.list {
  &__item {
    margin-bottom: 20px;
    font-style: italic;
    padding-left: 25px;
    background: url('../../img/point.png') 0px no-repeat;

    &--bold {
      font-weight: 600;
      font-style: normal;
    }
  }
}
