.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 50px;
  background-color: var(--dark-color);

  &__container {
    @include flex-all-sb;
  }

  &__logo {
    color: var(--light-color);
    font-weight: 800;
    font-size: 24px;
    text-transform: uppercase;
  }
}

@include tablet {
  .header {
    &__container {
      padding: 0;
    }
  }
}