.welcome { 
  padding: 295px 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: center;
  color: var(--grey-color);
  background-color: var(--dark-color);

  @include big-tablet {
    padding: 250px 0;
  }

  @include small-tablet {
    padding: 200px 0; 
  }

  @include big-mobile {
    padding: 150px 0;
  }

  @include mobile {
    padding-bottom: 100px;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-size: 44px;
    font-weight: 300;
    text-transform: uppercase;

    @include tablet {
      font-size: 40px;
    }

    @include small-tablet {
      font-size: 30px;
    }

    @include big-mobile {
      font-size: 25px;
    }

    @include mobile {
      font-size: 20px;
    }

    &--orange {
      font-weight: 600;
      color: var(--orange-color);
    }
  }

  &__text {
    margin: 0;
    font-size: 20px;
    font-weight: 300;

    @include big-tablet {
      font-size: 17px;
    }

    @include big-mobile {
      font-size: 14px;
      line-height: 1.5;
    }

    @include mobile {
      margin-top: 30px;
    }
  }

  
}



.text-bold {
    font-weight: 600;
}