.nav {
  &__list {
    @include flex-v-center;
  }

  &__item {
    margin-left: 40px;
  }

  &__link {
    padding-bottom: 5px;
    font-weight: 600;
    text-transform: uppercase;
    color: var(--light-color);

    &:hover {
      border-bottom: 3px solid var(--orange-color);
    }
  }
}

.burger {
  display: none;
}

@include big-tablet {
  .nav__link {
    display: none;
  }

  .burger {
    display: block;

    &__line {
      position: absolute;
      top: 50%;
      right: 8%;
      height: 4px;
      width: 26px;
      background-color: var(--light-color);
      
      &:first-child {margin-top: -5px;}

        &:last-child {
            margin-top: 5px;
        }
    }
  }
}