.form {
  position: relative;
  margin-bottom: 100px;
  color: var(--greyest-color);

  &__up {
    display: flex;
  }

  &__label {
    width: 50%;

    &:not(:last-child) {
      margin-right: 50px;
    }
  }

  &__input {
    margin-top: 10px;
    margin-bottom: 30px;
    width: 100%;
    height: 40px;
    font-size: 20px;
    border: 1px solid var(--grey-color);
    border-radius: 2px;
  }

  &__message {
    margin-top: 10px;
    height: 163px;

    width: 100%;
    border-color: var(--grey-color);
  }

  &__btn {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 165px;
    height: 40px;
    // padding: 15px 60px;
    transform: translateY(60px);

    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--orange-color);
  }
}