.services {
  display: flex;
  width: 100%;

  &-cards__item {
    padding: 0 52px;
  }

  // todo add range stile for border img

  &__image {
    margin: 0;
  }
}

.descr {
  margin: 0;
  margin-bottom: 100px;
  padding: 0 30px;
  text-align: center;
  font-size: 16px;
  font-weight: 300;
  font-style: italic;
  line-height: 1.5;
  color: var(--greyest-color);
}

.circle {
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 20px 18px 20px 19px;
  width: 100px;
  height: 100px;
  border: 4px solid var(--orange-color);
  border-radius: 100px;
}