html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  font-size: 14px;
  font-weight: 400;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.section__black {
  display: flex;
  width: 100%;
  background-color: var(--dark-color);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 50px;
  background-color: var(--dark-color);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  color: var(--light-color);
  font-weight: 800;
  font-size: 24px;
  text-transform: uppercase;
}

@media (max-width: 790px) {
  .header__container {
    padding: 0;
  }
}
.nav__list {
  display: flex;
  align-items: center;
}
.nav__item {
  margin-left: 40px;
}
.nav__link {
  padding-bottom: 5px;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--light-color);
}
.nav__link:hover {
  border-bottom: 3px solid var(--orange-color);
}

.burger {
  display: none;
}

@media (max-width: 900px) {
  .nav__link {
    display: none;
  }
  .burger {
    display: block;
  }
  .burger__line {
    position: absolute;
    top: 50%;
    right: 8%;
    height: 4px;
    width: 26px;
    background-color: var(--light-color);
  }
  .burger__line:first-child {
    margin-top: -5px;
  }
  .burger__line:last-child {
    margin-top: 5px;
  }
}
