.team {
  padding-bottom: 165px;
  color: var(--light-color);
}

.title {
  margin: 0;
  margin-top: 100px;
  margin-bottom: 100px;
  font-size: 44px;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;

  &__white {
    color: var(--light-color);
  }

  &::after {
    content: "";
    display: block;
    width: 100px;
    margin: 0 auto;
    margin-top: 30px;
    height: 4px;
    background-color: var(--orange-color);
  }

  &__bold {
    font-weight: 400;
  }
}

.cards {
  @include flex-all-sb;
  text-align: center;

  &__image {
    width: 130px;
    height: 130px;
    background-color: var(--grey-color);
    border-radius: 100px;
  }

  &__name {
    margin: 0;
    margin-bottom: 5px;
    font-size: 18px;
    font-weight: 400;
  }

  &__job {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 300;
  }

  &__info {
    font-weight: 300;
    line-height: 24px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 30px;
  }

  &__abiliti {
    margin: 0;
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: 400;
    text-transform: uppercase;
  }

  &__descr {
    margin: 0;
    line-height: 1.4;
    color: var(--greyest-color);
    margin-bottom: 100px;
  }
}

.image {
  margin: 0 auto;
  margin-bottom: 20px;
}