.swiper {
  color: var(--light-color);

  &__text {
    margin: 0;
    margin-bottom: 60px;
    text-align: center;
    padding: 0 100px;

    font-size: 18px;
    font-weight: 400;
    line-height: 1.4;
    text-transform: uppercase;
  }

  &__person {
    text-align: center;
    margin: 0;
    margin-bottom: 170px;
    color: var(--greyest-color);
    font-weight: 300;
  }
}

.grey__bold {
  font-weight: 400;
  color: var(--grey-color);
}